import React, { useState,useEffect } from 'react';
import './App.css';

function App() {
  const answerCheckers = [
    (input: string) => 'こたえ' === input,
    (input: string) => '解答' === input,
    (input: string) => /^(気|き)(候|こう)(変|へん)(動|どう)$/.test(input),
    (input: string) => /^(4|４)(6|６)$/.test(input)]
  const imagePaths = ['stage0.png','stage1.png','stage2.png','stage3.png']
  const clearTweetMessage = '#小泉謎 をすべて解き明かした！'
  const [stage,setStage] = useState<number>(0)
  const [inputValue,setInputValue] = useState<string>('')
  const [message,setMessage] = useState<string>('')
  const [imageSrc,setImageSrc] = useState<string>(imagePaths[0])
  const checkAnswer = () => {
    if (answerCheckers[stage](inputValue)) {
      setStage(stage+1)
      setImageSrc(imagePaths[stage+1])
      setInputValue('')
      setMessage('')
    } else {
      setMessage('不正解')
    }
  }
  useEffect(()=>{
    if (stage===4) {
      if ((window as any).twttr !== void 0) {
        (window as any).twttr.widgets.load(document.getElementById('twitter-share-button'));
      }
    }
  },[stage])
  return (
    <div className="App">
      <div>小泉謎</div>
      {
        stage < 4
         ? <>
            <div style={{marginTop: '32px'}}>
              <div>
                <form>
                  <input type="text" name="atext" value={inputValue}
                          onChange={(e) => setInputValue(e.target.value)}/>
                  <button type="submit" onClick={(event)=> {event.preventDefault(); checkAnswer()}}>解答</button>
                </form>
              </div>
              <div>
                {message}
              </div>
            </div>
            <div style={{marginTop: '32px'}}>
              <img className="image-1" src={'./'+imageSrc} alt="question"/>
            </div>
         </>
         : <>
          <div style={{marginTop: '32px'}}>{"小泉謎をすべて解き明かした！"}</div>
          <a
            id="twitter-share-button"
            className="twitter-share-button"
            href={`https://twitter.com/intent/tweet?text=${encodeURIComponent(clearTweetMessage)}&url=${encodeURIComponent("https://inrivity.github.io/koizumi/index.html")}`}>
            Tweet</a>
          </>
      }
    </div>
  );
}

export default App;
